<template>
    <nav>
        <v-app-bar flat app>
            <v-app-bar-nav-icon large class="ml-0 mr-4 pa-0 grey--text" @click.stop="drawer = !drawer"></v-app-bar-nav-icon>


            <v-toolbar-title 
            >
                <router-link 
                to="/" 
                class="ma-0 pa-0 text-uppercase grey--text title-link"
                >
                    <span class="font-weight-light">Todo</span>
                    <span>Skyarff</span>
                </router-link>
            </v-toolbar-title>

            <v-spacer></v-spacer>

            
            
            <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn
                        text
                        class="grey--text"
                        v-bind="attrs"
                        v-on="on"
                    >
                        <v-icon left>mdi-chevron-down</v-icon>
                        <span>Menu</span>
                    </v-btn>
                </template>

                <v-list>
                    <router-link
                        v-for="link in compLinks"
                        :key="link.text"
                        :to="link.route"
                        class="text-decoration-none red"
                    >
                        <v-list-item v-if="link.cond"
                            :class="['menu-hover', $route.path === link.route ? 'v-list-item--active' : '']"
                        >
                            <v-list-item-title>{{ link.text }}</v-list-item-title>
                        </v-list-item>
                    </router-link>
                </v-list>
            </v-menu>

            
            
            <v-btn 
            @click="$router.push('/signin')" 
            text color="grey"
            v-if="!this.$store.state.auth.userInfo.token"
            >
                <span>Sign In</span>
                <v-icon size="24" right>mdi-login</v-icon>

                
            </v-btn>

            <v-btn 
            @click="signOut" 
            text color="grey"
            v-else
            >
                <span>Sign out</span>
                <v-icon size="24" right>mdi-logout</v-icon>
            </v-btn>

        </v-app-bar>
        
        <v-navigation-drawer 
        v-model="drawer" 
        app 
        temporary
        class="primary"
        width="20vw"
        >
            <v-layout column align-center text-center class="pa-3">
                <v-flex class="mt-5">
                    <v-avatar size="60">
                        <img src="../assets/avatar3.jpg">
                    </v-avatar>
                    <p class="white--text mt-1 font-weight-thin">
                        Skyarff
                    </p>
                </v-flex>
                <v-flex class="mt-4 mb-3">
                    <!-- <Popup @snackbar="onSnackbar" /> -->
                </v-flex>
            </v-layout>
            <v-list>
                <router-link 
                v-for="link in compLinks" :key="link.text"
                :to="link.route"
                class="text-decoration-none"
                >
                    <v-list-item v-if="link.cond"
                    style="display: block;"
                    class="px-5 py-4 pl-10 list-tile-hover"
                    >
                        <div class="d-flex">
                            <v-icon class="mr-8">{{ link.icon }}</v-icon>
                            <v-list-item-content>
                                <v-list-item-title class=" white--text">{{ link.text }}</v-list-item-title>
                            </v-list-item-content>
                        </div>
                    </v-list-item>
                </router-link>
            </v-list>
        </v-navigation-drawer>
        


    </nav>
</template>

<script>
// import Popup from './PopupTodo.vue'

export default {
    components: {
        // Popup
    },
    data() {
        return {
            drawer: false,
        }
    },
    computed: {
        compLinks() {
            return [
                {icon: 'mdi-view-dashboard', text: 'Home', route: '/', cond: true},
                {icon: 'mdi-folder', text: 'Games', route: '/items', cond: this.$store.state.auth.userInfo.token},
                {icon: 'mdi-account', text: 'Team', route: '/team', cond: true},
            ]
        }
    },
    methods: {
        onSnackbar(str) {
            this.$emit('snackbar', str)
        },
        signOut() {
        this.$store.dispatch('auth/logout')
        localStorage.removeItem('userTokens')
        this.$router.push('/')
    }
    },
    
}
</script>

<style scoped>


.title-link {
  text-decoration: none;
}



.list-tile-hover {
    transition: background-color 0.25s ease
}

.list-tile-hover:hover {
    background-color: rgba(255, 255, 255, 0.08);
}

.menu-hover {
    transition: background-color 0.25s ease
}

.menu-hover:hover {
    background-color: rgba(150, 150, 150, 0.08);
}

</style>